import graceHeader from '../../graceUI/components/graceHeader.vue';

export default {
	props:['title'],
	components: {
		graceHeader
	},
	onLoad() {
		let that = this;
		//  高度自适应
		uni.getSystemInfo({
			success: function(res) {
				let calc = res.windowHeight - res.statusBarHeight - 44;
				that.winHeight = calc;
			}
		});
	}
}
